/* General Styles */
body {
  margin: 0;
  font-family: Arial, sans-serif;
  line-height: 1.6;
  color: #333;
}

a {
  text-decoration: none;
  color: inherit;
}

/* Navbar Styles */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 3px;
  flex-wrap: wrap;
}

.nav-links {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 2;
  transition: max-height 0.3s ease-in-out;
}

.nav-link {
  background-color:goldenrod;
  color:  #e6ba2a;
  text-decoration: none;
  margin: 0 15px;
  font-size: 15px;
}

.nav-link:hover {
  text-decoration: underline;
}

.nav-toggle {
  display: none;
  flex-direction: column;
  cursor: pointer;
}

.nav-toggle.bar {
  color:goldenrod;
  height: 3px;
  width: 25px;
  background-color:  #e6ba2a;
  margin: 4px 0;
  transition: 0.4s;
}

.nav-links.open {
  max-height: 300px;
}

.img-container {
  background: url("../../assets/patDoc.jpeg") no-repeat center center/cover;
}

/* Hero Section */
.hero-section {
  position: relative;
  color: #faf4e9;
  text-align: center;
  padding: 100px 20px;
}

.hero-section::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
}

.hero-content {
  position: relative;
  z-index: 2;
}

.hero-logo {
  height: 70px;
  margin-bottom: 20px;
  animation: app-logo-spin infinite 5s linear; /* Apply rotation animation */
}

@keyframes app-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(60deg);
  }
}

.hero-content h1 {
  font-size: 2.5rem;
  margin-bottom: 10px;
}

.hero-content p {
  font-size: 1.2rem;
  margin-bottom: 20px;
}

.cta-button {
  background-color: #0078d7;
  color: #fff;
  padding: 10px 20px;
  border-radius: 5px;
  text-decoration: none;
  font-size: 1rem;
}

.cta-button:hover {
  background-color: #005a9e;
}

/* Features Section */
.features-section {
  padding: 50px 20px;
  background-color: #fff;
  text-align: center;
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}

.feature {
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 5px;
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.feature:hover {
  transform: scale(1.05);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.feature-icon {
  font-size: 2.5em;
  color: #007bff;
  margin-bottom: 10px;
}

/* Testimonials Section */
.testimonials-section {
  padding: 50px 20px;
  background-color: #f9f9f9;
  text-align: center;
}

.testimonial {
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 5px;
  background-color: #fff;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.testimonial:hover {
  transform: translateY(-5px);
}

/* Footer */
.home-footer {
  padding: 30px 20px;
  background-color: #007bff;
  color: white;
  text-align: center;
}

.social-media a {
  margin: 0 15px;
  color: white;
  font-size: 1.5em;
  transition: color 0.3s ease, transform 0.3s ease;
}

.social-media a:hover {
  color: #ccc;
  transform: scale(1.2);
}

/* Responsive Design */
@media (max-width: 768px) {
  .nav-links {
    flex-direction: column;
    max-height: 0;
    overflow: hidden;
  }

  .nav-toggle {
    display: flex;
  }

  .nav-link {
    margin: 10px 0;
  }

  .features-grid {
    grid-template-columns: 1fr;
  }

  .testimonials-grid {
    grid-template-columns: 1fr;
  }

  .hero-content h1 {
    font-size: 2em;
  }

  .hero-content p {
    font-size: 1em;
  }

  .cta-button {
    font-size: 1em;
  }
}

/* Content Styles */
.container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  background-color: #faf4e9;

  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

h1,
h2,
h4 {
  text-align: center;
  color: #e6ba2a; /* Dark gray for headings */
}

li {
  margin-bottom: 15px;
}

.form-group {
  margin-bottom: 15px;
}

label {
  color: #333333;
  display: block;
  margin-bottom: 5px;
}

button {
  display: inline-block;
  height: 40px;
  width: 150px;
  border-radius: 20px;
  margin: 10px 0;
  background-color: #0078d7; /* Blue for buttons */
  color: white;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #005a9e; /* Darker blue on hover */
}

input,
textarea {
  height: 30px;
  width: 100%;
  border-radius: 5px;
  border: 1px solid #cccccc; /* Light gray border */
  padding: 5px 10px;
  margin-bottom: 10px;
  font-size: 16px;
}

textarea {
  resize: vertical;
  height: 100px;
}

.btn {
  display: inline-block;
  padding: 10px 20px;
  background-color: #0078d7;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
}

.btn:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.success-message {
  color: green;
  margin-top: 15px;
}

.error-message {
  color: red;
  margin-top: 15px;
}
